import React, { useState }  from "react"
import axios from "axios";
import { Link } from "gatsby"
import Layout from "../layout"
import { Typography } from 'antd'
const MyForm = () => {
    
    const [serverState, setServerState] = useState({
      submitting: false,
      status: null
    });
    const handleServerResponse = (ok, msg, form) => {
      setServerState({
        submitting: false,
        status: { ok, msg }
      });
      if (ok) {
        form.reset();
      }
    };
    const handleOnSubmit = e => {
      e.preventDefault();
      const form = e.target;
      setServerState({ submitting: true });
      axios({
        method: "post",
        url: "https://getform.io/f/2385bd8d-e1db-4320-a546-55b77024cc88",
        data: new FormData(form)
      })
        .then(r => {
          handleServerResponse(true, "Thanks!", form);
        })
        .catch(r => {
          handleServerResponse(false, r.response.data.error, form);
        });
    };
    return (

      
         <div type="submit" className="col-md-12 mt-5">
            <h3 className='text-primary-color'>        Contact Me!       </h3>
            <form onSubmit={handleOnSubmit}>
            <div className="form-group">
            <Typography.Title level={4} className='text-gray-600'>Email Address</Typography.Title>
                <input type="email" name="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Your Email"/>
            </div> 
            <div className="form-group">
            <Typography.Title level={4} className='text-gray-600'>Name</Typography.Title>
                <input type="text" name="name" className="form-control" id="exampleInputName" placeholder="Your Name" required="required"/>
            </div>
            <div className="form-group">
            <Typography.Title level={4} className='text-gray-600'>Message</Typography.Title>
                <textarea type="text" name="message" className="form-control" id="exampleInputName" placeholder="Your Message" rows='5' required="required"/>
            </div>
            <button type="submit" className="btn btn-dark"  disabled={serverState.submitting}>
                Submit
            </button>
            {serverState.status && (
                <p className={!serverState.status.ok ? "errorMsg" : ""}>
                {serverState.status.msg}
                </p>
            )}
            </form>
        </div>

    

    );
  };
  
  export default MyForm;